import uniMediaList from '@/components/uni-media-list/uni-media-list.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';

import {
	friendlyDate
} from '@/common/util.js';

export default {
	components: {
		uniMediaList,
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: '上拉加载更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			scrollLeft: 0,
			refreshing: false,
			refreshText: '下拉可以刷新',
			newsList: [],
			tabIndex: 0,
			tabBars: [],
			likeTabs: [],
			hasLike: false
		}
	},
	computed: {
		scrollViewHeight() {
			return 'height:' + (uni.getSystemInfoSync().windowHeight) + 'px';
		}
	},
	onLoad: function() {
		uni.showLoading({
			title: '加载中'
		});
		const value = uni.getStorageSync('likeTabs');
		if (value) {
			this.likeTabs = value;
			this.hasLike = true;
		}
		// 初始化列表信息
		this.getCategory();
	},
	methods: {
		getCategory() {
			uni.request({
				url: 'https://hellogithub.com//api/v1/category/',
				header: {
					'Authorization': 'Basic 32321312312312312312312'
				},
				success: (result) => {
					uni.hideLoading();
					this.tabBars = result.data.payload;
					console.log(this.tabBars)
					this.tabBars['6'].name = 'JS 项目';
					this.tabBars['8'].name = 'Obj-C 项目';
					if (this.hasLike) {
						let tabBars1 = [];
						let tabBars2 = [];
						this.tabBars.forEach((tabBar) => {
							tabBars1[tabBar.id] = tabBar.name
						})
						this.likeTabs.forEach((likeTab) => {
							tabBars2.push({
								id: likeTab,
								name: tabBars1[likeTab]
							})
						})
						this.tabBars = tabBars2;
					}
					this.tabBars.forEach((tabBar) => {
						this.newsList.push({
							data: [],
							refreshing: false,
							refreshFlag: false,
							refreshText: "",
							requestParams: {
								catid: tabBar.id,
								page: 1,
							},
							has_more: true,
							loadingText: '加载中...'
						});
					});
					this.getList()
				}
			});
		},
		getList(action = 1) {
			let activeTab = this.newsList[this.tabIndex];
			activeTab.requestParams.time = new Date().getTime() + '';
			if (action === 1) {
				this.newsList[this.tabIndex].requestParams.page = 1;
			}
			uni.request({
				url: 'https://hellogithub.com//api/v1/category/projects/?q={"id":' + this.newsList[this.tabIndex].requestParams.catid +
					', "page":' + this.newsList[this.tabIndex].requestParams.page + '}',
				method: "GET",
				header: {
					'Authorization': 'Basic 32321312312312312312312'
				},
				success: (result) => {
					if (result.statusCode == 200) {
						const data = result.data.payload.map((news) => {
							return {
								category: news.category,
								sort_desc: news.sort_desc,
								img_url: news.img_url,
								name: news.name,
								update_time: news.update_time,
								volume: news.volume,
								url: news.url
							};
						});
						if (action === 1) {
							activeTab.data = data;
							this.refreshing = false;
						} else {
							data.forEach((news) => {
								activeTab.data.push(news);
							});
						}
						this.newsList[this.tabIndex].requestParams.page++;
						this.newsList[this.tabIndex].has_more = result.data.has_more;
						if (!this.newsList[this.tabIndex].has_more) {
							this.newsList[this.tabIndex].loadingText = '没有更多数据了';
						}
					}
				}
			});
		},
		goDetail(detail) {
			console.log(detail)
			uni.setClipboardData({
				data: detail.url,
				success: function() {
					uni.showToast({
						title: '项目地址已复制',
						duration: 2000
					});
				}
			});
		},
		// dislike(tabIndex, newsIndex) {
		// 	uni.showModal({
		// 		content: '不感兴趣？',
		// 		success: (res) => {
		// 			if (res.confirm) {
		// 				this.newsList[tabIndex].data.splice(newsIndex, 1);
		// 			}
		// 		}
		// 	})
		// },
		loadMore() {
			console.log('load more');
			this.getList(2);
		},
		async changeTab(event) {
			let index = event.detail.current;
			if (this.isClickChange) {
				this.tabIndex = index;
				this.isClickChange = false;
				return;
			}
			let tabBar = await this.getElSize('tab-bar');
			let tabBarScrollLeft = tabBar.scrollLeft;
			let width = 0;

			for (let i = 0; i < index; i++) {
				let result = await this.getElSize(this.tabBars[i].ref);
				width += result.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth,
				nowElement = await this.getElSize(this.tabBars[index].ref),
				nowWidth = nowElement.width;
			if (width + nowWidth - tabBarScrollLeft > winWidth) {
				this.scrollLeft = width + nowWidth - winWidth;
			}
			if (width < tabBarScrollLeft) {
				this.scrollLeft = width;
			}
			this.isClickChange = false;
			this.tabIndex = index;

			// 首次切换后加载数据
			const activeTab = this.newsList[this.tabIndex];
			if (activeTab.data.length === 0) {
				this.getList();
			}
		},
		getNodeSize(node) {
			return new Promise((resolve, reject) => {
				dom.getComponentRect(node, (result) => {
					resolve(result.size);
				});
			});
		},
		onRefresh(event) {
			this.refreshText = '正在刷新...';
			this.refreshing = true;
			this.getList();
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				// 					let tabBar = await this.getElSize('tab-bar'),
				// 						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				// 					this.scrollLeft = tabBarScrollLeft;
				// 					this.isClickChange = true;
				console.log('ssss')
				this.tabIndex = index;
				// 首次切换后加载数据
				const activeTab = this.newsList[this.tabIndex];
				if (activeTab.data.length === 0) {
					this.getList();
				}
			}
		},
	}
}
